<template>
    <div class="num_list">
        <num-item
            v-for="(num, index) in numArr"
            :key="index"
            :offset="num"
        ></num-item>
    </div>
</template>
<script>
import NumberItem from "./NumberItem";
export default {
    name: "i-number-run",
    props: {
        num: {
            type: Number,
            default: 0,
        },
    },
    data() {
        return {
            lastNum: 0,
        };
    },
    components: {
        "num-item": NumberItem,
    },
    computed: {
        numArr() {
            let { num, lastNum } = this,
                lastNums = (lastNum + "").split(""),
                nums = (num + "").split(""),
                offsets;
            offsets = nums.map((num, index) => {
                let ago = lastNums[index] || 0;
                return {
                    time: num - ago === 0 ? num : num - ago,
                    long: num,
                };
            });
            // this.lastNum = num;
            return offsets;
        },
    },
};
</script>
<style scoped lang="less">
.num_list {
    display: flex;
    justify-content: flex-start;
}
</style>
