import Nav from "./nav";
import RunningNum from "./runningNum";

const components = {
    Nav,
    RunningNum,
};

const install = function(Vue, opts = {}) {
    Object.values(components).forEach((component) => {
        Vue.component(component.name, component);
    });
};
export default {
    version: "1.0.0",
    install,
    ...components,
};
