import VueRouter from "vue-router";
import Vue from "vue";
Vue.use(VueRouter);
import ME from "../ME.vue";
import Web from "../Web.vue";
import MobileWeb from "../MobileWeb.vue";
const router = new VueRouter({
    routes: [
        {
            path: "/",
            component: ME,
        },
        {
            path: "/mobile",
            component: MobileWeb,
        },
        {
            path: "/web",
            component: Web,
        },
    ],
});

export default router;
